<script context="module" lang="ts">

    import {error} from './Logger.svelte'

    
    export function rand(min: number, max: number) {
        min = Math.ceil(min);
        max = Math.floor(max + 1);
        return Math.floor(Math.random() * (max - min) + min);
    }

    export function randFrom<T>(arr: readonly T[]): T {
        return arr[rand(0, arr.length-1)];
    }

    export function trueFalse(): boolean {
        return rand(0, 1) == 0;
    }

    export function randSign(): number {
        return rand(0, 1) == 0 ? +1 : -1;
    }

    export function pm(num: number): number {
        return num * (rand(0, 1) == 0 ? +1 : -1);
    }

    export function shuffle<T>(src: readonly T[]): T[] {
        let dst = [...src];
        for (let i = dst.length - 1; i > 0; i--) {
            const j = Math.floor(Math.random() * (i + 1));
            [dst[i], dst[j]] = [dst[j], dst[i]];
        }
        return dst;
    }

    let audio = new AudioContext();

    export function beep() {
        var osc = audio.createOscillator();
        var gain = audio.createGain();

        osc.connect(gain);
        gain.connect(audio.destination);

        gain.gain.value = 0.5;
        osc.frequency.value = 440;
        osc.type = "triangle";

        osc.start();

        setTimeout(() => osc.stop(), 50);
    } 

    export function run(task: () => void) {
        setTimeout(task, 0);
    }

    export function delay(ms: number) {
        return new Promise(resolve => setTimeout(resolve, ms));
    }

    export function range(start: number, end: number) {
        return Array.from({length: (end - start)}, (_, k) => k + start);
    }

    export function safe(func: () => void) {
        try {
            return func();
        }
        catch (err) {
            let name = func.name;
            if (name === "") name = "unknown";
            error(`Error in ${name}`, err);
        }
    }

    export function splitNoEmpty(str: string, delim?: string) {
        return str.trim().split(delim ?? " ").filter(Boolean);
    }

</script>
