<script lang="ts">

    import { pulse } from '../core/Pulse.svelte';
    import { getBgColor } from '../core/Grid.svelte';

    type State = "start" | "clicked" | "right" | "wrong";

    export let ga: string;
    export let value: number;
    export let bgColor = getBgColor(ga, "bg-gray-100");
    export let sz: number = 30;

    let _text: string;
    let _sz: number;
    let _bgColor: string;

    export function setState(state: State) {
        switch (state) {

            case "start":
                _text = "??";
                _bgColor = bgColor;
                break;

            case "clicked":
                _text = value.toLocaleString();
                _bgColor = bgColor;
                break;

            case "right":
                _text = value.toLocaleString();
                _bgColor = "bg-green-200";
                break;

            case "wrong":
                _text = value.toLocaleString();
                _bgColor = "bg-red-200";
                break;
        }

        _sz = sz - 1.2 * _text.length;
    }

</script>


<button
    class="{_bgColor}"
    style="grid-area:{ga};font-size:{_sz}px" 
    use:pulse
    on:click
>
    {_text}
</button>
