<script lang="ts">

    import { pulse } from '../core/Pulse.svelte';
    
    import {on, fire,} from './Model.svelte'
    import type {Game} from './Model.svelte'

    let _text: string
    let _class: string

    export let ga: string;
    export let index: number
    export let sz: string = "30px"

    function onClick() {
        fire("--evt-answer", {index});
    }

    function getText(game: Game): string {
        if (game.state === "first-click")
            return "??";

        return game.answers[index].toString();
    }

    function getClass(game: Game) {
        if (game.state === "first-click")
            return "bg-gray-100";

        if (game.state === "active")
            return "bg-orange-200";

        if (game.rightAt === index)
            return "bg-green-200";

        if (game.clickedAt !== index)
            return "bg-gray-100";

        return "bg-red-200";
    }

    function updateView(game: Game) {
        _text = getText(game);
        _class = getClass(game);
    }

    on("--cmd-update-views", updateView);

</script>

<button 
    class={_class}
    style="grid-area:{ga};font-size:{sz}" 
    on:click={onClick}
    use:pulse
>
    {_text}
</button>