<script lang="ts">

    import { rand, shuffle, range, pm, trueFalse, randFrom } from "../core/Helpers.svelte";
    import { Anki } from "../core/Anki.svelte"
    import { Mp3 } from "../core/Mp3.svelte";
    import Grid from './../core/Grid.svelte'
    import Clock from './../core/Clock.svelte'
    import Congrats from '../core/Congrats.svelte';

    import { on, fire } from "./Model.svelte"
    import { newTimedCmd } from "./Model.svelte";
    import Count from "./Count.svelte"
    import Num from "./Num.svelte"
    import Ans from "./Ans.svelte"
    import Op from "./Op.svelte"

    import type { Config, Round } from "./Model.svelte"

    let congrats: Congrats;

    let _game: Round;

    let _current = 0;
    let _rounds: Round[] = [];

    $: _roundsLeft = _rounds.length - _current;

    let _config = Anki.getConfig<Config>();

    let _tcNewRound = newTimedCmd("--cmd-next-round", 1000);
    let _tcIdle = newTimedCmd("--cmd-idle", 7000, 3000);

    let answers: Ans[] = [null, null, null];

    interface AbPair {
        a, b, f: number;
    }

    function getAbPair(): AbPair {
        let line = Anki.getCard().content.split(",").map(x => parseInt(x));

        let a = rand(1, 9);
        let b = rand(2, 9);
        let f = b + pm(1);

        let noOfZeros = rand(line[0], line[1]);

        for (let no = noOfZeros; no-- > 0; ) {
            if (a === 1) {                  // ensure first factor is not one
                a *= 10;
                continue;
            }

            switch (trueFalse()) {
                case true:
                    a *= 10;
                    break;
                case false:
                    b *= 10;
                    f *= 10;
                    break;
            }
        }

        return {a, b, f};
    }

    function addMulRound() {
        let op = "x";

        let pair = getAbPair();

        let lhs = pair.a;
        let rhs = pair.b;

        let a0 = lhs * rhs;
        let a1 = lhs * pair.f;

        let a2, a3: number;

        switch (trueFalse()) {
            case true:
                a2 = a0 * 10;
                a3 = a1 * 10;
                break;
            case false:
                a2 = a0 / 10;
                a3 = a1 / 10;
                break;
        }

        let nums = shuffle([lhs, rhs]);
        let answers = shuffle([a0, a1, a2, a3]);

        let rightAt = answers.findIndex(x => x == a0);

        _rounds.push({
            nums, op, answers, rightAt
        });
    }

    function addDivRound() {
        let op = "/";

        let pair = getAbPair();

        let lhs = pair.a * pair.b;
        let rhs = pair.a;

        let a0 = pair.b;
        let a1 = pair.f;

        let a2, a3: number;

        switch (trueFalse()) {
            case true:
                a2 = a0 * 10;
                a3 = a1 * 10;
                break;
            case false:
                a2 = (a0 >= 10) ? a0 / 10 : a0 + pm(1);
                a3 = (a1 >= 10) ? a1 / 10 : a1 + pm(1);
                break;
        }

        while ([a0, a1, a2].find(x => x === a3))           // can happen
            a3++;

        let nums = [lhs, rhs];
        let answers = shuffle([a0, a1, a2, a3]);

        let rightAt = answers.findIndex(x => x === a0);

        _rounds.push({
            nums, op, answers, rightAt
        });
    }

    function nextRound() {
        _tcNewRound.cancel();
        _tcIdle.reset();

        if (++_current === _rounds.length) {
            addMulRound();
            addDivRound();
        }

        _game = _rounds[_current];

        answers.forEach(ans => ans.setBgColor("bg-gray-100"));
    }

    function init() {
        _tcIdle.reset();

        let noOfRounds = _config.rounds ?? 4;

        for (let cnt = noOfRounds; cnt-- > 0; )
        {
            addMulRound();
            addDivRound();
        }

        _game = _rounds[_current];
    }

    function select(no: number) {
        _tcIdle.reset();

        if (_game.clickedAt !== undefined) {
            fire("--cmd-next-round");
            return;
        }

        _game.clickedAt = no;

        if (no === _game.rightAt) {
            if (_current == _rounds.length - 1) {
                Mp3.play("done");
                congrats.play();
            } else {
                Mp3.play("yes");
            }
            _tcNewRound.reset();
        } else {
            Mp3.play("no");
            answers[no].setBgColor("bg-red-200");
            addMulRound();
            addDivRound();
            _roundsLeft = _rounds.length - _current;
        }

        answers[_game.rightAt].setBgColor("bg-green-200");
    }

    function onClick() {
        if (_game.clickedAt !== undefined)
            fire("--cmd-next-round");
    }

    function idle() {
        _tcIdle.reset();
        Mp3.play("idle");
    }

    on("--cmd-next-round", () => nextRound());
    on("--evt-click", () => onClick());
    on("--cmd-idle", () => idle());
    
    init();

    let layout = {
        areas: `
            "cnt   .    clk "
            "lhs   op   rhs "
            "a0    .    a1  "
            "a2    .    a3  "
        `,
        cols: `2fr 1fr 2fr`,
        rows: `60px 80px 80px 80px`,
        bgColors: {
            cnt: "bg-purple-300",
            lhs: "bg-green-100",
            op:  "bg-yellow-200",
            rhs: "bg-green-100"
        }
    };

</script>

<Grid {layout}
    on:click={() => fire("--evt-click")}
>
    <Count ga="cnt" count={_roundsLeft}/>
    <Clock ga="clk" />
    <Num ga="lhs" num={_game.nums[0]} />
    <Op  ga="op"  op={_game.op} />
    <Num ga="rhs" num={_game.nums[1]} />

    {#each [0, 1, 2, 3] as i}
        <Ans 
            ga={`a${i}`} 
            num={_game.answers[i]} 
            bind:this={answers[i]} 
            on:click={() => select(i)}
        />
    {/each}

    <Congrats bind:this={congrats} />
</Grid>
