<script lang="ts">

    import { pulse } from '../core/Pulse.svelte';
    import { getBgColor } from '../core/Grid.svelte';

    export let ga: string;
    export let values: number[];

    $: _value = values[0];
    $: _max = values[1];
    $: _left = _max - _value;
    $: _bar = 100 * (_value / _max)

    let _bgColor = getBgColor(ga);

</script>

<div 
    class="w-full bg-red-50 text-right"
    use:pulse
>
    <div 
        class= " \
            text-xs text-center text-black \
            {_bgColor} \
        "
        style="width:{_bar}%"
    >
    <span use:pulse>
        {_left}
    </span>
    </div>
</div>
