<script lang="ts">

    import { pulse } from '../core/Pulse.svelte';

    export let ga: string;
    export let num: number;

    export let sz: number = 30

    $: _text = num.toLocaleString();
    $: _scale = _text.length < 5 ? 1.2 : 1.5;
    $: _sz = sz - _scale * _text.length;

    let _bgColor = "bg-gray-100";

    export function setBgColor(value: string) {
        _bgColor = value;
    }

</script>


<button
    class="{_bgColor}"
    style="grid-area:{ga};font-size:{_sz}px" 
    on:click
    use:pulse
>
    {num.toLocaleString()}
</button>
