<script lang="ts">

    import { onMount } from "svelte";

    import { Mp3 } from "../core/Mp3.svelte";
    import AsciiMath from "../core/AsciiMath.svelte"
    import Clock from './../core/Clock.svelte'
    import Grid from "../core/Grid.svelte"
    import Progress from "../core/Progress.svelte"
    import Congrats from '../core/Congrats.svelte';

    import { Game } from "./Game.svelte"
    import { newTimedCmd } from "./Model.svelte";
    import Ans from "./Ans.svelte"
import { on } from "../plus-over/Model.svelte";
import TimedCmd from "../core/TimedCmd.svelte";

    let congrats: Congrats;

    let _game = Game;
    let _round = _game.nextRound();
    let _answers: Ans[] = [null, null, null, null];
    let _state: "start" | "clicked" | "answered";
    let _progress: number[] = [1, 10];
    let _tcNewRound = newTimedCmd("--cmd-new-round", 1000);
    let _tcIdle = newTimedCmd("--cmd-idle", 10000, 3000);

    function equClick() {
        Mp3.playRandom();
    }

    function anyClick(): boolean {
        _tcIdle.reset();

        if (_state == "start") {
            _state = "clicked";
            _answers.forEach(ans => ans.setState("clicked"));
            return true;
        }

        if (_state === "clicked")
            return false;

        if (_state == "answered") {
            newRound();
            return true;
        }

        return true;
    }

    function addRounds(count: number) {
        _game.addRounds(count);
        _progress[0] += 1;
        _progress[1] += count;
    }

    function gotAnswer(value: number) {
        _tcIdle.reset();

        if (anyClick())
            return;

        if (_state === "clicked") {
            _state = "answered";

            let answerAt = _round.answers.findIndex(x => x === value); 
            let rightAt = _round.answers.findIndex(x => x === _round.rightAns);

            if (answerAt === rightAt) {
                _progress[0] += 1;
                let isLast = _game.isLast(_round);
                Mp3.play(isLast ? "done" : "yes");
                _tcNewRound.reset();
                if (isLast)
                    congrats.play();
            } 
            else {
                _answers[answerAt].setState("wrong");
                Mp3.play("no");
                addRounds(2);
            }

            _answers[rightAt].setState("right");
        }
    }

    function newRound() {
        _round = _game.nextRound();
        startRound();
    }

    function idle() {
        Mp3.play("idle");
        _tcIdle.reset();
    }

    function startRound() {
        _state = "start";

        _tcIdle.reset();
        _tcNewRound.cancel();

        _answers.forEach(ans => {
            ans.setState("start");
        });

        _progress = _round.progress;
    }

    onMount(() => startRound());

    let layout = {
        areas: `
            "prg   .    clk "
            "equ   equ  equ"
            "a0    .    a1  "
            "a2    .    a3  "
        `,
        cols: `2fr 1fr 2fr`,
        rows: `60px 80px 80px 80px`,
        bgColors: {
            prg: "bg-green-300",
            equ: "bg-yellow-50"
        }
    };

    on("--cmd-new-round", () => newRound());
    on("--cmd-idle", () => idle());

</script>

<Grid {layout}
    on:click={() => anyClick()}
>
    <Progress ga="prg" values={_progress} />

    <Clock ga="clk" />

    <AsciiMath 
        ga="equ"
        sz={40}
        exp={_round.equ}
        on:click={() => equClick()}
    />
    
    {#each _round.answers as ans, no}
        <Ans 
            ga={`a${no}`} 
            value={ans}
            bind:this={_answers[no]} 
            on:click={() => gotAnswer(ans)}
        />
    {/each}

    <Congrats bind:this={congrats} />
</Grid>
