<script context="module" lang="ts">

    import { Anki } from "../core/Anki.svelte"
    import { pm, rand, randFrom, shuffle, splitNoEmpty, trueFalse } from "../core/Helpers.svelte";
    import { newFsm } from "../core/FSM.svelte";
    import type { IFSM } from "../core/FSM.svelte"
    import { error } from "../core/Logger.svelte";

    import type { Round, GameState, GameEvent, Config } from "./Model.svelte"

    type GameType = "equ+-" | "equ*/";

    interface IGame {
        readonly fsm: IFSM<GameState, GameEvent>;
        isLast(round: Round): boolean;
        addRounds(count: number);
        nextRound(): Round;
    }

    let _patterns: string[];
    let _next: number;

    let _fsm = newFsm<GameState, GameEvent>({
        exclusiveStates: [
            ["start", "clicked", "answered"],
            ["right", "wrong"]
        ],
        on: [],
        onEnter: {
        },
        onTransit: {
        },
        transitions: [
        ]
    });

    let _config = Anki.getConfig<Config>();
    let _totalRounds = _config.rounds;
    let _gameType: GameType = "equ+-";
    let _a: number[] = [10, 90];
    let _b: number[] = [10, 90];
    let _x: string = 'X';
    let _noOverflow: boolean = false;

    function newAbc(): number[] {

        let tzero = n => n - n % 10;

        if (_gameType === "equ+-") {
            let a0 = rand(1, 9);
            let b0 = _noOverflow ? rand(0, 9 - a0) : rand(10 - a0, 9);
            let a = tzero(rand(_a[0], _a[1])) + a0;
            let b = tzero(rand(_b[0], _b[1])) + b0;
            let c = a + b;

            return [
                ...shuffle([a, b]), 
                c
            ];
        }

        error(`Unknown game type '${_gameType}'`);
        return [];
    }

    function makeEqu(pattern: string, abc: number[]): [string, number] {

        function isVar(ch: string): boolean {
            let code = ch.charCodeAt(0);
            return code >= 'A'.charCodeAt(0) && 
                   code <= 'Z'.charCodeAt(0);
        }

        let chars = [...pattern];
        let vars = chars.filter(isVar);

        let v = randFrom(vars);

        let equ = pattern.replace(v, 'x');
        let ans = abc[v.charCodeAt(0) - 'A'.charCodeAt(0)];

        equ = equ
            .toLocaleLowerCase()
            .replace('a', abc[0].toLocaleString())
            .replace('b', abc[1].toLocaleString())
            .replace('c', abc[2].toLocaleString())
            .replace('x', _x);

        return [equ, ans];
    }

    class CGame implements IGame {
        constructor() {
            for (let cmd of Anki.commands()) {
                switch (cmd[0].toLocaleLowerCase()) {
                    case "equ+-":       _gameType = "equ+-";    break;
                    case "equ*/":       _gameType = "equ*/";    break;
                    case "no-overflow": _noOverflow = true;     break;
                    case "x":           _x = cmd[1];            break;
                    case "a":
                        _a = [
                            parseInt(cmd[1]), 
                            parseInt(cmd[2])
                        ]
                        break;
                    case "b":
                        _b = [
                            parseInt(cmd[1]), 
                            parseInt(cmd[2])
                        ]
                        break;
                }
            }
            _patterns = Anki.tokens();
            _next = -1;
        }

        get fsm() {
            return _fsm;
        }

        isLast() {
            return _next + 1 >= _totalRounds;
        }

        addRounds(count: number) {
            _totalRounds += count;
        }

        nextRound(): Round {
            _next++;

            let pattern = _patterns[_next % _patterns.length];
            let abc = newAbc();

            let [equ, rightAns] = makeEqu(pattern, abc);

            let answers = shuffle([
                rightAns,
                rightAns + pm(10),
                rightAns + pm(rand(1, 1)),
                rightAns - pm(rand(2, 2))
            ]);

            let progress = [
                _next < _totalRounds ? _next : _totalRounds,
                _totalRounds
            ];

            return {
                progress,
                equ,
                rightAns,
                answers
            };
        }
    }

    export const Game: IGame = new CGame();

</script>